.TodoItem {
    padding: .5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.TodoItem > .title {
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 0 .5em;
    font-family: "Microsoft Yahei", sans-serif;
    font-weight: lighter;
}

.TodoItem > button {
    font-size: 12px;
    padding: .3em .6em;
    border-radius: 10px;
    outline: none;
    border: none;
    color: rgba(247, 247, 247, .9);
    background: lightseagreen;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.TodoItem > button:hover {
    color: rgba(247, 247, 247, 1);
    background: #1ea8a0;
}







