.UserDialog-Wrapper {
    /* 全屏 */
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: -webkit-gradient(linear, left bottom, right top, from(rgba(222, 222, 222, 0)), to(rgba(0, 0, 0, .8)));
    background: -webkit-linear-gradient(bottom left, rgba(222, 222, 222, 0), rgba(0, 0, 0, .8));
    background: -o-linear-gradient(bottom left, rgba(222, 222, 222, 0), rgba(0, 0, 0, .8));
    background: linear-gradient(to top right, rgba(222, 222, 222, 0), rgba(0, 0, 0, .8));
    /* 居中 */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.UserDialog {
    min-width: 16em;
    border-radius: 16px;
    background: white;
    -webkit-box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
            box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
    background: #fff;
}

.UserDialog:before {
    content: "";
    display: block;
    height: 8px;
    border-radius: 16px 16px 0 0;
    background: -webkit-gradient(linear, left top, right top, from(RGB(195, 226, 121)), color-stop(RGB(247, 254, 200)), color-stop(RGB(255, 208, 105)), color-stop(RGB(242, 118, 105)), color-stop(RGB(220, 156, 190)), to(RGB(197, 154, 224)));
    background: -webkit-linear-gradient(left, RGB(195, 226, 121), RGB(247, 254, 200), RGB(255, 208, 105), RGB(242, 118, 105), RGB(220, 156, 190), RGB(197, 154, 224));
    background: -o-linear-gradient(left, RGB(195, 226, 121), RGB(247, 254, 200), RGB(255, 208, 105), RGB(242, 118, 105), RGB(220, 156, 190), RGB(197, 154, 224));
    background: linear-gradient(to right, RGB(195, 226, 121), RGB(247, 254, 200), RGB(255, 208, 105), RGB(242, 118, 105), RGB(220, 156, 190), RGB(197, 154, 224));
}

.UserDialog > nav {
    height: 2.5em;
    line-height: 2.5;
    position: relative;
}

.UserDialog > nav label {
    font-size: 1em;
    font-weight: lighter;
    color: #aaa;
    text-align: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: absolute;
    width: 50%;
    top: 0;
    bottom: 0;
    background: RGB(247, 247, 247);
}

.UserDialog > nav label[for="signUp"] {
    background: rgba(222, 222, 222, .6);
    -webkit-box-shadow: inset 0px 0px 1px 0px rgba(0, 0, 0, .2);
            box-shadow: inset 0px 0px 1px 0px rgba(0, 0, 0, .2);
}

.UserDialog > nav label:nth-child(1) {
    left: 0
}

.UserDialog > nav label:nth-child(2) {
    right: 0;
}

.UserDialog > nav input {
    display: none;
}

.UserDialog > .panes label {
    display: none;
}

.UserDialog > .panes input {
    font-weight: lighter;
    font-size: 1em;
    display: block;
    margin: 1em auto;
    outline: none;
    border: none;
    border-bottom: 1px solid #aaa;
}

.UserDialog > .panes button {
    font-weight: lighter;
    font-size: 1em;
    color: rgb(247, 247, 247);
    display: block;
    margin: 1em auto;
    padding: .4em 1.6em;
    border-radius: 6px;
    outline: none;
    border: none;
    background: lightcoral;
    cursor: pointer;
}

.UserDialog > .panes button:hover {
    background: #f37070;
}



